import { Component, OnInit } from '@angular/core';
import {Router,ActivatedRoute} from '@angular/router';
import {AddproService} from '../addpro.service';
import {PaymentsService} from '../payments.service';

@Component({
  selector: 'app-usergetcart',
  templateUrl: './usergetcart.component.html',
  styleUrls: ['./usergetcart.component.css']
})
export class UsergetcartComponent implements OnInit {

  constructor(private _paymentsService:PaymentsService,private activatedRoute:ActivatedRoute,private _addproService:AddproService, private _router : Router) { }
products;
debitdataUser = { }
paytmdataUser = { }
netbankingdataUser = { }
creditdataUser = { }
// products;
public ShippingCharges = 150;

public continue:boolean = false;
public buttonname:any = 'Continue';
public buy:boolean = false;
public buttonNa:any = 'Buy';
public show:boolean = false;
public buttonName:any = 'Show';
public credit:boolean = false;
public btnName:any = 'Credit';
public netbanking:boolean = false;
public buttonName1:any = 'NetBanking';
public paytm:boolean = false;
public buttonName2:any = 'Paytm';
public debitcard:boolean = false;
public buttonName3:any = 'Debit Card';

debitdata(){
  this._paymentsService.debitdataUser(this.debitdataUser)
  .subscribe(
    res => {
      console.log(res)
      localStorage.setItem('token',res.token)
            
      },
    err => console.log(err)
  )
}
paytmdata(){
  this._paymentsService.paytmdataUser(this.paytmdataUser)
  .subscribe(
    res=>{
      console.log(res)
      localStorage.setItem('token',res.token)
    },
    err=>console.log(err)
  )
}
netbankingdata(){
  this._paymentsService.netbankingdataUser(this.netbankingdataUser)
  .subscribe(
    res=>{
      console.log(res)
      localStorage.setItem('token',res.token)
    },
    err=>console.log(err)
  )
}
creditdata(){
  this._paymentsService.creditdataUser(this.creditdataUser)
  .subscribe(
    res=>{
      console.log(res)
      localStorage.setItem('token',res.token)
    },
    err=>console.log(err)
  )
}
reloading():void{
  window.location.reload();
}
accData(){
  this.continue = !this.continue;
  if(this.continue) 
  this.buttonname = "Continue";
  else
  this.buttonname = "Continue";
}
getData() {
  this.show = !this.show;

  
  if(this.show)  
    this.buttonName = "Show";
  else
    this.buttonName = "Show";
}
loadingData(){
  this.buy = ! this.buy;
  if(this.buy)
  this.buttonName = "Buy";
  else
  this.buttonName = "Buy";
}
toggle() {
  this.credit = !this.credit;
  if(this.credit)  
    this.btnName = "Credit";
  else
    this.btnName = "Credit";
}
toggle1() {
  this.netbanking = !this.netbanking;

  // CHANGE THE NAME OF THE BUTTON.
  if(this.netbanking)  
    this.buttonName1 = "NetBanking";
  else
    this.buttonName1 = "NetBanking";
}
toggle2() {
  this.paytm = !this.paytm;
  if(this.paytm)  
    this.buttonName2 = "Paytm";
  else
    this.buttonName2 = "Paytm";
}
toggle3() {
  this.debitcard = !this.debitcard;
  if(this.debitcard)  
    this.buttonName3 = "Debit Card";
  else
    this.buttonName3 = "Debit Card";
}



ngOnInit() {
    let id=this.activatedRoute.snapshot.params["_id"];
    this._addproService.getProductsByID(id).subscribe(
      (response)=>{
        this.products=response;
        console.log("products",this.products);
      },
      (err)=>{
        this.products=err;
      }
    );
   
  }

}
