import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';
@Injectable({
  providedIn: 'root'
})
export class PaymentsService {
  private _creditURL = "http://localhost:4000/api/creditdata"
  private _debitURL ="http://localhost:4000/api/debitdata"
  private _netbankingURL = "http://localhost:4000/api/netbankingdata"
  private _paytmURL ="http://localhost:4000/api/paytmdata"
  constructor(private http : HttpClient,private _router:Router) { }
  creditdataUser(credit){
    return this.http.post<any>(this._creditURL,credit)
  }
  debitdataUser(debit){
    return this.http.post<any>(this._debitURL,debit)
  }
  netbankingdataUser(netbanking){
    return this.http.post<any>(this._netbankingURL,netbanking)
  }
  paytmdataUser(paytm){
    return this.http.post<any>(this._paytmURL,paytm)
  }
  getToken(){
    return localStorage.getItem('token')
  }
}
